*{
  font-family: 'Courier New', Courier, monospace;
  font-size: 1.3rem;
}

body{
  color: white;
  background-color: #21232A;
  padding: 70px;
}

input{
  font-size: 1.1rem !important;
  width: 100%;
  color: #21232A;
}

.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card{
  align-content: center;
  border: 1px solid gray;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
}

.card p{
  padding: 5px;
  text-align: center;
}

.card img{
  max-height: 170px; 
}
